<template>
  <div class="container">

    <sidebar :author="detail.author" from="Topic" /><!-- sidebar -->

    <!-- main -->
    <div class="main">
      <!--  -->
      <div class="panel">
        <div class="article">
          <div class="article-head">
            <span class="tag light" v-if="detail.good || detail.top">{{ detail | tag }}</span>
            <span class="full-title h1">{{detail.title}}</span>
            <div class="props">
              <span> {{ detail.create_at | fromNow }}</span>
              <span> <router-link :to="{name: 'User', params: { loginname : detail.author.loginname }}">{{ detail.author.loginname }}</router-link></span>
              <span>{{ detail.visit_count }} </span>
              <span> {{ detail.last_reply_at | fromNow }}</span>
              <span> {{ detail.tab | comeFrom }}</span>
            </div>
            <!--  / -->
            <div class="user-control-bar" v-if="userInfo.loginname == detail.author.loginname">
              <router-link :to="{name: 'Release', params: { topic_id: detail.id }}" title="" >
                <i class="el-icon-edit"></i>
              </router-link>
            </div>
            <!--  -->
            <div class="collect" v-if="isLogin">
              <el-button :type="detail.is_collect ? '' : 'success' " @click="collectTopic()">{{ detail.is_collect ? '' : '' }}</el-button>
            </div>
          </div>
          <div class="acticle-body markdown-body" v-html="detail.content"></div>
        </div>
      </div>
      <!--  end -->

      <!--  -->
      <div class="panel" v-if="detail.reply_count > 0">
        <div class="head">
          <span>{{detail.reply_count}} </span>
        </div>
        <div class="reply-list">
          <div class="reply-item" v-for="(item, index) in detail.replies" :key="item.id" :id="item.id">

            <div class="reply-item-head">
              <span class="user-info">
                <span class="avatar-img">
                  <router-link :to="{ name: 'User', params: {loginname: item.author.loginname}}">
                    <img :src="item.author.avatar_url" />
                  </router-link>
                </span>
                <span><router-link :to="{ name: 'User', params: {loginname: item.author.loginname}}">{{item.author.loginname}}</router-link></span>
                <span class="c-aaa">{{ index + 1 }}</span>
                <span class="c-aaa">{{item.create_at | fromNow }}</span>
                <span class="tag light" v-if="detail.author.loginname == item.author.loginname"></span>
              </span>

              <span class="user-action">
                <span class="like" :class="[item.is_uped ? 'static' : '']">
                  <i class="fa"
                    :class="[item.is_uped ? 'fa-heart' : 'fa-heart-o']"
                    title=""
                    @click="like(item.id, item.author, index)">
                  </i>
                  <span class="like-count c-aaa" v-if="item.ups.length > 0">
                    {{item.ups.length}}
                  </span>
                </span>
                <span class="reply" v-if="isLogin">
                  <i class="fa fa-reply" title="" @click="replyOther(item.author.loginname)"></i>
                </span>
              </span>
            </div>

            <div class="reply-item-body">
              <div class="markdown-body" v-html="item.content"></div>
            </div>
          </div>
        </div>
      </div><!--  end -->

      <!--  -->
      <!--   v-if="isLogin"  simpleMDE (SimpleMDE: Error. No element was found.)
         :class="{ hidden: !isLogin }" -->
      <div class="panel reply-area" :class="{ hidden: !isLogin }">
        <div class="head"></div>
        <div class="body bg-fff reply-from">
          <el-form>
            <el-form-item>
              <el-input type="textarea" id="markdownEditor"></el-input>
            </el-form-item>
            <div style="padding:0 0 15px 15px;">
              <el-button type="primary" @click="reply">{{replyBtnTxt}}</el-button>
            </div>
          </el-form>
        </div>
      </div>

    </div><!-- main end -->
  </div>
</template>

<script>
import API_CONFIG from '../../api/cnodejs.api' // coodejsapi
import Axios from 'axios'
import hljs from 'highlight.js'
import 'highlight.js/styles/default.css'
import { mapState } from 'vuex'
import SimpleMDE from 'simplemde'

export default {
  name: 'Topic',
  data () {
    return {
      detail: {
        id: '',
        author_id: '',
        author: {
          loginname: '--',
          avatar_url: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAACAQMAAACnuvRZAAAAA1BMVEX29vYACyOqAAAACklEQVQI12MAAgAABAABINItbwAAAABJRU5ErkJggg=='
        },
        title: '',
        content: '',
        create_at: Date.now(),
        last_reply_at: Date.now(),
        tab: '',
        good: false,
        top: false,
        reply_count: 0,
        visit_count: 0,
        replies: [],
        is_collect: false
      },
      replyBtnTxt: ''
    }
  },
  beforeRouteEnter (to, from, next) {
    //  confirm 
    //  `this`
    // 
    next(vm => {
      //  `vm` 
      vm.featchTopicDetail()
    })
  },
  computed: {
    ...mapState(['isLogin', 'accessToken', 'userInfo'])
  },
  methods: {
    // 
    initEditor () {
      this.simplemde = new SimpleMDE({
        element: document.getElementById('markdownEditor'),
        autoDownloadFontAwesome: true,
        spellChecker: false,
        status: false
      })
    },

    // 
    featchTopicDetail () {
      Axios.get(`${API_CONFIG.topicDetail}${this.$route.params.id}`, {
        params: {
          accesstoken: this.accessToken || undefined
        }
      }).then(res => {
        if (res.data.success) {
          this.detail = res.data.data
          this.$nextTick(() => { //  DOM  DOM
            var code = document.querySelectorAll('.markdown-body code')
            for (var i = 0; i < code.length; i++) {
              hljs.highlightBlock(code[i])
            }
          })
        }
      }).catch(e => {
        this.$message.error('')
        this.$router.replace('/')
      })
    },

    // 
    collectTopic () {
      Axios.post(this.detail.is_collect ? API_CONFIG.topicDeCollect : API_CONFIG.topicCollect, {
        accesstoken: this.accessToken,
        topic_id: this.detail.id
      }).then(res => {
        if (res.data.success) {
          if (this.detail.is_collect) {
            // 
            this.detail.is_collect = false //  false
          } else {
            // 
            this.detail.is_collect = true //  true
          }
        }
      }).catch(e => {
        this.$message.error('id')
      })
    },

    // 
    collectDeTopic () {
      Axios.post(API_CONFIG.topicDeCollect, {
        accesstoken: this.accessToken,
        topic_id: this.detail.id
      }).then(res => {
        if (res.data.success) {
          // this.$message.success('')
          this.detail.is_collect = false
        }
      }).catch(e => {
        this.$message.error('id')
      })
    },

    /**
     * methods: 
      post /topic/:topic_id/replies 
       post 
      accesstoken String  accessToken
      content String 
      reply_id String 
      
      {success: true, reply_id: '5433d5e4e737cbe96dcef312'}
    */
    reply () {
      if (this.replyBtnTxt === '...') return // 
      var val = this.simplemde.value()
      if (!val) { // 
        return this.$message.error('')
      }
      this.replyBtnTxt = '...'
      Axios.post(`${API_CONFIG.reply}${this.detail.id}/replies`, {
        accesstoken: this.accessToken,
        content: val
      }).then(res => {
        if (res.data.success) {
          this.replyBtnTxt = ''
          this.$message.success('')
          this.featchTopicDetail() // topic
          this.simplemde.value('') // textarea
        }
      }).catch(e => {
        this.replyBtnTxt = ''
      })
    },

    /**
     * methods: 
     * post /reply/:reply_id/ups 
     *  post 
     * accesstoken String
     *  action up or down
     * 
     * {"success": true, "action": "down"}
     * @param {replyId}
     * @param {author}
     * @param {index}
     */
    like (replyId, author, index) {
      if (!this.isLogin) {
        return this.$message.warning('')
      }
      if (author.loginname === this.userInfo.loginname) {
        return this.$message.warning('')
      }
      Axios.post(`${API_CONFIG.like}${replyId}/ups`, {
        accesstoken: this.accessToken
      }).then(res => {
        if (res.data.success) {
          if (res.data.action === 'up') {
            // up  
            this.detail.replies[index].ups.push(this.userInfo.id) // 
            this.detail.replies[index].is_uped = true // 
          } else {
            // down  
            this.detail.replies[index].ups.pop()
            this.detail.replies[index].is_uped = false // 
          }
        }
      }).catch(e => {
        this.$message.warning('')
      })
    },
    // @
    replyOther (loginname) {
      var top = document.querySelector('.reply-from').offsetTop
      window.scrollTo(0, top)
      this.simplemde.value(`@${loginname} `)
    }

  },
  // 
  filters: {
    // /
    tag: function (detail) {
      if (detail.top) {
        return ''
      }
      if (detail.good) {
        return ''
      }
    },
    // tab
    comeFrom: function (tab) {
      switch (tab) {
        case 'ask':
          return ''
        case 'good':
          return ''
        case 'share':
          return ''
        case 'dev':
          return ''
        case 'job':
          return ''
        default:
          return 'cnode'
      }
    }
  },

  mounted () {
    this.initEditor()
  }

}
</script>

<style>
.editor-toolbar{
  border: 0;
  border-bottom: 1px solid #e0e0e0;
}
.CodeMirror{
  border: 0;
}
.CodeMirror,.CodeMirror-scroll{
  min-height: 160px;
}
.el-textarea__inner{
  border: none;
}
</style>



